import React from 'react';
import { reduxForm } from 'redux-form';
import s from './ProfileInfo.module.css';
import { createField, Input, Textarea } from '../../Common/FormControls/FormControls';
import styles from '../../Common/FormControls/FormControls.module.css'


let ProfileDataForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <button>save</button>
            </div>
            {props.error && <div className={styles.stobSubmitForm}>{props.error}</div>}
            <div>
                <b>Full name</b>: {createField([], 'fullName', "Full Name", Input)}
            </div>
            <div>
                <b>Looking for a job</b>: {createField([], 'lookingForAJob', "", Input, { type: 'checkbox' })}
            </div>
            <div>
                <b>My Proffesional skills</b>: {createField([], 'lookingForAJobDescription', 'My Proffesional skills', Textarea)}
            </div>
            <div>
                <b>About me</b>: {createField([], 'aboutMe', 'About Me', Textarea)}
            </div>
            <div>
                <b>Contacts</b>:{Object.keys(props.profile.contacts).map(key => {
                    return <div key={key} className={s.contact}>
                        <b>{key}: {createField([], 'contacts.' + key, key, Input)}</b>
                    </div>
                })}
            </div>
        </form>
    )
}

let ProfileDataFormReduxForm = reduxForm({ form: 'edit-profile' })(ProfileDataForm)

export default ProfileDataFormReduxForm;