import React, { useState } from 'react';
import Preloader from '../../Common/Preloader/Preloader';
import s from './ProfileInfo.module.css';
import ProfileStatus from './ProfileStatus';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';
import userPhoto from '../../../assets/avatar.png'
import ProfileDataForm from './ProfileDataForm';

const ProfileInfo = (props) => {

  let [editMode, setEditMode] = useState(false);

  if (props.profile === null) {
    return <Preloader />
  }

  let onMainPhotoSelected = (e) => {
    if (e.target.files.length) {
      props.savePhoto(e.target.files[0])
    }
  }

  const onSubmit = (formData) => {
    props.saveProfile(formData).then(
      () => {
        setEditMode(false);
      }
    );
  }

  return (
    <div>
      <div className={s.description}>
        {!props.profile.photos.large && !props.profile.photos.small
          ? <img src={userPhoto} />
          : <img src={props.profile.photos.large} />}

        {props.isOwner && <input onChange={onMainPhotoSelected} type='file' />}

        {editMode ? <ProfileDataForm initialValues={props.profile} onSubmit={onSubmit} profile={props.profile} /> : <ProfileData isOwner={props.isOwner} goToEditMode={() => { setEditMode(true) }} profile={props.profile} />}

        <ProfileStatusWithHooks status={props.status} updateProfileStatus={props.updateProfileStatus} />
      </div>
    </div>
  );
}

let Contact = ({ contactTitle, contactValue }) => {
  return <div className={s.contact}>
    <b>{contactTitle}</b>: {contactValue}
  </div>
}

let ProfileData = (props) => {
  return (
    <div>
      <div>{
        props.isOwner && <div><button onClick={props.goToEditMode}>edit</button></div>}</div>
      <div>
        <b>Full name</b>: {props.profile.fullName}
      </div>
      <div>
        <b>Looking for a job</b>: {props.profile.lookingForAJob ? "yes" : "no"}
      </div>
      {
        props.profile.lookingForAJob &&
        <div>
          <b>My Proffesional skills</b>: {props.profile.lookingForAJobDescription}
        </div>
      }
      <div>
        <span>{props.profile.aboutMe}</span>
      </div>
      <div>
        <b>Contacts</b>:{Object.keys(props.profile.contacts).map(key => {
          return <Contact key={key}
            contactTitle={key}
            contactValue={props.profile.contacts[key]} />
        })}
      </div>
    </div>
  )
}


export default ProfileInfo;